import Dialog from '@/wxcomponents/dist/dialog/dialog';
import Toast from '@/wxcomponents/dist/toast/toast';
export default {
	data() {
		return {
			show: false,
			showDelect: false,
			columns: ['一班', '二班', '三班'],
			className: "一班",
			classLists: []
		}
	},
	methods: {
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		// picker选择器
		onConfirm(event) {
			const {
				picker,
				value,
				index
			} = event.detail;
			console.log((`当前值：${value}, 当前索引：${index}`));
			this.show = false;
			this.className = value
		},
		onCancel() {
			this.show = false;
		},
		// 改变班级
		changeRanking() {
			console.log(222)
			this.show = true;
		},
		// 根据当前用户获取班级列表
		classList() {
			this.$minApi.classList({}).then(data => {
				this.classLists = data.data[0]
				this.className = data.data[0].className
				console.log(data)
			})
		},
		// 查看详情
		viewDetails(studentId) {
			uni.navigateTo({
				url: '../../home/details/studentDetails?studentId=' + studentId
			})
		},
		// 删除班级
		delectClass() {
			this.showDelect = true
			console.log(this.showDelect)
		},
		// 删除班级弹框确认按钮
		onConfirmDel() {
			console.log("确认")
			this.showDelect = false
			// console.log(this.showDelect)
			// 删除班级
			this.$minApi.classDelete({}).then(data => {
				if (data.code == 200) {
					Toast("删除成功")
				} else {
					Toast(data.msg)
				}
				console.log(data)
			})
		},
		// 删除班级弹框取消按钮
		onCancelDel() {
			console.log("取消")
			this.showDelect = false
			// console.log(this.showDelect)
		},
		// 修改班级信息页面跳转
		linkTo() {
			uni.navigateTo({
				url: 'revisionClass'
			})
		}

	},
	created() {
		this.classList()
	},
	onLoad(options) {
		console.log(options)
	}

}


